.app-dashboard, .modal {
  .footer{
      padding: 24px 0;

      &.footer-default{
          background-color: #f2f2f2;
      }

      nav{
          display: inline-block;
          float: left;
          padding-left: 0;
      }

      ul{
          margin-bottom: 0;
          padding: 0;
          list-style: none;

          li{
              display: inline-block;

              a{
                  color: inherit;
                  padding: $padding-base-vertical;
                  font-size: $font-size-small;
                  text-transform: uppercase;
                  text-decoration: none;

                  &:hover{
                      text-decoration: none;
                  }
              }
          }
      }

      .copyright{
          font-size: $font-size-small;
          line-height: 1.8;
      }

      &:after{
          display: table;
          clear: both;
          content: " ";
      }
  }
}
