.app-dashboard, .modal {
  .fixed-plugin{
      position: fixed;
      right: 0;
      width: 64px;
      background: rgba(0,0,0,.3);
      z-index: 1031;
      border-radius: 8px 0 0 8px;
      text-align: center;
      top: 120px;

      li > a,
      .badge{
          transition: all .34s;
          -webkit-transition: all .34s;
          -moz-transition: all .34s;
      }

      .fa-cog{
          color: #FFFFFF;
          padding: 10px;
          border-radius: 0 0 6px 6px;
          width: auto;
      }

      .dropdown-menu{
          right: 80px;
          left: auto !important;
          top: -52px !important;
          width: 290px;
          border-radius: 10px;
          padding: 0 10px;
      }

      .dropdown .dropdown-menu .nc-icon{
        top: 2px;
        right: 10px;
        font-size: 14px;
      }

      .dropdown-menu:after,
      .dropdown-menu:before{
          right: 10px;
          margin-left: auto;
          left: auto;
      }

      .fa-circle-thin{
          color: #FFFFFF;
      }

      .active .fa-circle-thin{
          color: #00bbff;
      }

      .dropdown-menu > .active > a,
      .dropdown-menu > .active > a:hover,
      .dropdown-menu > .active > a:focus{
          color: #777777;
          text-align: center;
      }

      img{
          border-radius: 0;
          width: 100%;
          height: 100px;
          margin: 0 auto;
      }

      .dropdown-menu li > a:hover,
      .dropdown-menu li > a:focus{
          box-shadow: none;
      }

      .badge{
          border: 3px solid #FFFFFF;
          border-radius: 50%;
          cursor: pointer;
          display: inline-block;
          height: 23px;
          margin-right: 5px;
          position: relative;
          width: 23px;

        &.badge-light {
          border: 1px solid $light-gray;

          &.active,
          &:hover {
            border: 3px solid #0bf;
          }
        }
      }

      .badge.active,
      .badge:hover{
          border-color: #00bbff;
      }

      .badge-blue{
          background-color: $brand-info;
      }
      .badge-green{
          background-color: $brand-success;
      }
      .badge-orange{
          background-color: $brand-primary;
      }
      .badge-yellow{
          background-color: $brand-warning;
      }
      .badge-red{
          background-color: $brand-danger;
      }

      h5{
          font-size: 14px;
          margin: 10px;
      }

      .dropdown-menu li{
          display: block;
          padding: 15px 2px;
          width: 25%;
          float: left;
      }

      li.adjustments-line,
      li.header-title,
      li.button-container{
          width: 100%;
          height: 35px;
          min-height: inherit;
      }

      li.button-container{
          height: auto;

          div{
              margin-bottom: 5px;
          }
      }

      #sharrreTitle{
          text-align: center;
          padding: 10px 0;
          height: 50px;
      }

      li.header-title{
          height: 30px;
          line-height: 25px;
          font-size: 12px;
          font-weight: 600;
          text-align: center;
          text-transform: uppercase;
      }

      .adjustments-line{
          p{
              float: left;
              display: inline-block;
              margin-bottom: 0;
              font-size: 1em;
              color: #3C4858;
          }

          a{
              color: transparent;

              .badge-colors{
                  position: relative;
                  top: -2px;
              }

              a:hover,
              a:focus{
                  color: transparent;
              }
          }

          .togglebutton{
              text-align: center;

              .label-switch{
                position: relative;
                left: -10px;
                font-size: $font-size-mini;
                color: $default-color;

                &.label-right{
                  left: 10px;
                }
              }

              .toggle{
                  margin-right: 0;
              }
          }

          .dropdown-menu > li.adjustments-line > a{
                padding-right: 0;
                padding-left: 0;
                border-bottom: 1px solid #ddd;
                border-radius: 0;
                margin: 0;
          }
      }



      .dropdown-menu{
          > li{
              & > a.img-holder{
                    font-size: 16px;
                    text-align: center;
                    border-radius: 10px;
                    background-color: #FFF;
                    border: 3px solid #FFF;
                    padding-left: 0;
                    padding-right: 0;
                    opacity: 1;
                    cursor: pointer;
                    display: block;
                    max-height: 100px;
                    overflow: hidden;
                    padding: 0;

                    img{
                       margin-top: auto;
                    }
              }

              a.switch-trigger:hover,
              & > a.switch-trigger:focus{
                  background-color: transparent;
              }

              &:hover,
              &:focus{
                  > a.img-holder{
                      border-color: rgba(0, 187, 255, 0.53);;
                  }
              }
          }

          > .active > a.img-holder,
          > .active > a.img-holder{
              border-color: #00bbff;
              background-color: #FFFFFF;
          }

      }

      .btn-social{
          width: 50%;
          display: block;
          width: 48%;
          float: left;
          font-weight: 600;
      }

      .btn-social{
          i{
              margin-right: 5px;
          }

          &:first-child{
              margin-right: 2%;
          }
      }

      .dropdown{
          .dropdown-menu{
            transform-origin: 0 0;

            &:before{
               border-bottom: 16px solid rgba(0, 0, 0, 0);
               border-left: 16px solid rgba(0,0,0,0.2);
               border-top: 16px solid rgba(0,0,0,0);
               right: -27px;
               bottom: 425px;
            }

            &:after{
               border-bottom: 16px solid rgba(0, 0, 0, 0);
               border-left: 16px solid #FFFFFF;
               border-top: 16px solid rgba(0,0,0,0);
               right: -26px;
               bottom: 425px;
            }

            &:before,
            &:after{
               content: "";
               display: inline-block;
               position: absolute;
               width: 16px;
               transform: translateY(-50px);
               -webkit-transform: translateY(-50px);
               -moz-transform: translateY(-50px);
            }
          }

        &.show-dropdown .show{
          .dropdown-menu .show{
            transform: translate3d(0, -60px, 0)!important;
            bottom: auto!important;
            top: 0!important;
          }
        }
      }

      .bootstrap-switch{
          margin:0;
      }
  }

  .fixed-plugin {
    .show-dropdown {
      .dropdown-menu[x-placement=bottom-start] {
        @include transform-translate-y-fixed-plugin (-100px);

        &:before,
        &:after {
          top: 100px;
        }
      }
      .dropdown-menu[x-placement=top-start] {
        @include transform-translate-y-fixed-plugin (100px);
      }

      &.show {
        .dropdown-menu.show[x-placement=bottom-start] {
          @include transform-translate-y-fixed-plugin (-60px);
        }

        .dropdown-menu.show[x-placement=top-start] {
          @include transform-translate-y-fixed-plugin (470px);
        }
      }
    }
  }
}
